<template>
  <main>
    <div :class="['form-box', { active }]" style="background-color: red">
      <h5>Sign In</h5>
      <ApolloMutation
        :mutation="require('../graphql/signin.gql')"
        :variables="{
          username,
          password,
        }"
        @done="onDone"
      >
        <template v-slot="{ mutate, loading, error }">
          <form>
            <label>Username:</label>
            <input type="text" v-model="username" required />

            <label>Password:</label>
            <input type="password" v-model="password" required />

            <input
              :disabled="loading"
              @click="mutate()"
              type="submit"
              value="Continue"
            />

            <p v-if="error">An error occurred: {{ error }}</p>
          </form>
        </template>
      </ApolloMutation>

      <div @click="active = true">I want to Sign Up</div>
    </div>
    <div :class="['form-box', { active }]" style="background-color: purple">
      <h5>Sign Up</h5>
      <ApolloMutation
        :mutation="require('../graphql/signup.gql')"
        :variables="{
          username: rUsername,
          password: rPassword,
          email: rEmail
        }"
        @done="signUpDone"
      >
        <template v-slot="{ mutate, loading, error }">
          <form>
            <label>Username:</label>
            <input type="text" required v-model="rUsername" />

            <label>Email:</label>
            <input type="text" required v-model="rEmail" />

            <label>Password:</label>
            <input type="password" required v-model="rPassword" />

            <label>Re-Type Password:</label>
            <input type="password" required v-model="cPassword" />

            <input
              :disabled="loading"
              @click="mutate()"
              type="submit"
              value="Continue"
            />
            <p v-if="error">An error occurred: {{ error }}</p>
          </form>
        </template>
      </ApolloMutation>
      <div @click="active = false">I want to Sign In</div>
    </div>
  </main>
</template>


<script>

export default {
  name: "SignupForm",
  props: {
    msg: String,
  },
  data() {
    return {
      active: false,
      username: "",
      password: "",
      rUsername: "",
      rPassword: "",
      cPassword: "",
      rEmail: ""
    };
  },
  methods: {
    onDone() {
      this.$router.push("/launchpad");
    },

    signUpDone() {
      this.$router.push("/register");
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
main {
  background-color: royalblue;
  color: white;
  width: 300px;
  max-width: 300px;
  height: 500px;
  overflow: hidden;
}

.form-box {
  min-width: 280px;
  height: 100%;
  display: inline;
  position: relative;
  left: 0;
}

form {
  text-align: left;
  width: 175px;
  margin: 0 auto;
}

.active {
  left: -260px;
}
</style>
