import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import BusinessRegistration from '@/views/BusinessRegistration'
import Launchpad from '@/views/Launchpad'
import CreateClient from '@/views/subLaunchpad/CreateClient'
import ManageClient from '@/views/subLaunchpad/ManageClient'
import Report from '@/views/subLaunchpad/Report'
import ClientProfile from '@/views/subLaunchpad/ClientProfile'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/register',
    name: 'BusinessRegistration',
    component: BusinessRegistration
  },
  {
    path: '/launchpad',
    name: 'Launchpad',
    component: Launchpad,
    children: [
      {
        // UserProfile will be rendered inside User's <router-view>
        // when /user/:id/profile is matched
        path: 'create',
        name: 'Create',
        component: CreateClient
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'manage',
        name: 'Manage',
        component: ManageClient
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'profile/:id',
        name: 'ClientProfile',
        component: ClientProfile,
        props: true
      },
      {
        // UserPosts will be rendered inside User's <router-view>
        // when /user/:id/posts is matched
        path: 'report',
        name: 'Report',
        component: Report
      }
    ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
