<template>
  <main>
    <ApolloMutation
      :mutation="require('../graphql/registerBusiness.gql')"
      :variables="{
        name: bizName,
        regNumber,
        bizAbbr,
        cell,
        address,
        bizEmail,
        postalAddr,
        bankAccName,
        bankAccNumber: parseInt(bankAccNumber),
        bankAccType,
        branchCode,
      }"
      @done="onDone"
    >
      <template v-slot="{ mutate, loading, error }">
        <form v-on:submit.prevent="mutate()">
          <div :class="['one', { active }]">
            <div class="form-inner">
              <label>What's your business name?</label>
              <input type="text" required v-model="bizName" />

              <label>What's your business registration number?</label>
              <input type="password" required v-model="regNumber" />

              <label>What's your business cellphone number?</label>
              <input type="text" required v-model="cell" />

              <label>What's your business address?</label>
              <input type="text" required v-model="address" />

              <label>What's your email address?</label>
              <input type="email" required v-model="bizEmail" />

              <label>What's your postal address?</label>
              <input type="text" required v-model="postalAddr" />

              <button @click="active = true">Next</button>
            </div>
          </div>

          <div :class="['two', { active }]">
            <div class="form-inner">
              <label>What's your business bank account name?</label>
              <input type="text" required v-model="bankAccName" />

              <label>What's your bank account number?</label>
              <input type="text" required v-model="bankAccNumber" />

              <label>What's your business account branch code?</label>
              <input type="text" required v-model="branchCode" />

              <label>Abbreviated business name?</label>
              <input type="text" required v-model="bizAbbr" />

              <label>Account Type:</label>
              <select v-model="bankAccType">
                <option value="CHECQUE">Cheque</option>
                <option value="SAVINGS">Savings</option>
              </select>

              <button :disabled="loading">Complete</button>

              <p v-if="error">An error occurred: {{ error }}</p>
            </div>
          </div>
        </form>
      </template>
    </ApolloMutation>

    <section></section>
  </main>
</template>

<script>
export default {
  name: "BusinessRegistration",
  components: {},
  data() {
    return {
      active: false,
      bizName: "",
      regNumber: "",
      bizAbbr: "",
      cell: "",
      address: "",
      bizEmail: "",
      postalAddr: "",
      bankAccName: "",
      bankAccNumber: "",
      bankAccType: "",
      branchCode: "",
    };
  },
  methods: {
    onDone() {
      this.$router.push("/launchpad");
    },
  },
};
</script>

<style scoped>
main {
  display: flex;
  height: 100vh;
}
form {
  min-width: 600px;
  max-width: 600px;
  height: 100%;
  max-height: 100%;
  display: flex;
  text-align-last: left;
  overflow-x: hidden;
  overflow-y: scroll;
}

section {
  flex-grow: 1;
  height: 100%;
  background-color: rebeccapurple;
}

.one,
.two {
  min-width: 600px;
  height: 100%;
  display: inline;
  position: relative;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

input {
  width: 300px;
  box-sizing: border-box;
}

.form-inner {
  display: flex;
  flex-direction: column;
}

button {
  padding: 10px 40px;
  margin: 0 auto;
  align-self: center;
  border-radius: 5px;
  box-shadow: none;
}

select {
  width: 300px;
}
.active {
  left: -600px;
}
</style>

